// Helper functtions

const animationStarting = (section) => {
  // console.log(section);
  if (!isMobile()) {
    section.find('.op-0').removeClass('op-0');
  } else if (isMobile() && section.find('.op-0').length)
    section.find('.op-0').removeClass('op-0');
};

function isMobile() {
  return innerWidth < 1025 ? true : false;
}

function isDesktop() {
  return !isMobile();
}

export {
  animationStarting,
  isMobile,
  isDesktop,
};
